import { Icon48WritebarDone, Icon56ErrorOutline, Icon56Users3Outline } from '@vkontakte/icons';
import bridge from '@vkontakte/vk-bridge';
import { ConfigProvider, AdaptivityProvider, AppRoot, Button, Div, ModalCard, ModalRoot, ScreenSpinner, Snackbar, View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import React, { useEffect, useState, useRef } from 'react';
import io from 'socket.io-client';
import Home from './panels/Home.js';
import './panels/css/global.css';
import Error from './panels/error.js';


const App = () => {
	const adsCooldown = useRef(0);
	const adsCooldownTimer = useRef(null);
	const socket_link = 'https://nueta-production-d002.up.railway.app/'
	const [screen, setscreen] = useState('home')
	const [activeunder, setactiveunder] = useState('home');
	const [loading, setloading] = useState(false)
	const [popout, setpopout] = useState(<ScreenSpinner />)
	const [online, setonline] = useState(0)
	const [datatop, settop] = useState([])
	const [uniquecode, setuniquecode] = useState('')
	const [fetchedUser, setUser] = useState({
		id: 0,
		first_name: '',
		last_name: '',
		photo_url: ''
	})
	const [userdata, setuserdata] = useState({
		balance: 0,
		bonus: 0,
		ban: 0
	})
	const [activemodal, setmodal] = useState(null)
	const [errorscreen, seterrorscreen] = useState({
		'num_error': 0,
		'error_msg': ''
	})
	const [snackbar, setsnackbar] = useState(null)
	const [connection, setconnection] = useState(0)
	const [metop, setmetop] = useState([])

	useEffect(() => {
		const tg = window.Telegram.WebApp;

		if (!tg || !tg.initDataUnsafe || !tg.initDataUnsafe.user) {
			window.location.href = "https://t.me/wave_stars_bot"; // –≤–µ—Ä–Ω—ë–º –≤ –±–æ—Ç–∞
		}
		if (tg) {
			Telegram.WebApp.expand();
		}
		
	}, []);


	useEffect(async () => {
		async function fetchData() {
			window.addEventListener('popstate', e => e.preventDefault() & menu());
			window.history.pushState({ screen: 'home' }, `home`);
			const tg = window.Telegram.WebApp;
			tg.ready(); // –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –≤—ã–∑–≤–∞—Ç—å
			const unsafeData = tg.initDataUnsafe;
			console.log(unsafeData.user)

			setTimeout(() => {
				setUser(unsafeData.user)
				connect(unsafeData.user)
				setpopout(null)
			}, 2345);

			// –í—Å—Ç–∞–≤—å —ç—Ç–æ –≤ useEffect
			const script = document.createElement("script");
			script.src = "//libtl.com/sdk.js";
			script.dataset.zone = "9491456";
			script.dataset.sdk = "show_9491456";
			document.body.appendChild(script);

		};
		fetchData()
	}, [])

	useEffect(async () => {
		async function bridgecon() {
			bridge.subscribe((e) => {
				switch (e.detail.type) {
					case 'VKWebAppViewHide':
						setpopout(<ScreenSpinner size="large" />)
						window.socket.disconnect()
						break;
					case 'VKWebAppViewRestore':
						window.socket.connect()
						setpopout(null)
						break;
					case 'VKWebAppAccessTokenFailed':
						setpopout(null)
						setscreen('home')
						break;
					default:
						break;
				}
			})
		}
		bridgecon()
	}, [screen])


	// –¢–∞–π–º–µ—Ä —Å –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ–º snackbar –∫–∞–∂–¥—É—é —Å–µ–∫—É–Ω–¥—É
	useEffect(() => {
		let interval;
		if (adsCooldown > 0) {
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FFA000" />}
				>
					–†–µ–∫–ª–∞–º–∞ –¥–æ—Å—Ç—É–ø–Ω–∞ —á–µ—Ä–µ–∑ {adsCooldown} —Å–µ–∫.
				</Snackbar>
			);

			interval = setInterval(() => {
				setAdsCooldown(prev => {
					if (prev <= 1) {
						clearInterval(interval);
						setsnackbar(null);
						return 0;
					}
					return prev - 1;
				});
			}, 1000);
		}
		return () => clearInterval(interval);
	}, [adsCooldown]);


	const connect = (data) => {
		window.socket = io.connect(socket_link, {
			autoConnect: false,
			secure: true,
			query: {
				"uid": data.id,
				"nick": data.first_name + ' ' + data.last_name,
				"photo": data.photo_url,
				"params": window.location.search.substring(1),
				'nowcode': uniquecode
			},
			transports: ['websocket'],
			timeout: 5000,
			reconnection: false, // –æ—Ç–∫–ª—é—á–∞–µ–º –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏–µ –ø–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è
		});

		events(window.socket);

		window.socket.on('connect_error', (error) => {
			seterrorscreen({
				'num_error': 2,
				'error_msg': '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏–µ –∫ —Å–µ—Ä–≤–µ—Ä—É! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.'
			});
			setscreen('error');
			setmodal(null);
			setpopout(null);
			setconnection(0);
			// –∑–¥–µ—Å—å –Ω–µ –≤—ã–∑—ã–≤–∞–µ–º window.socket.connect() –ø–æ–≤—Ç–æ—Ä–Ω–æ, –ø–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–µ–Ω–∏–π –Ω–µ –±—É–¥–µ—Ç
		});

		window.socket.on('connect_timeout', () => {
			seterrorscreen({
				'num_error': 2,
				'error_msg': '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏–µ –∫ —Å–µ—Ä–≤–µ—Ä—É! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.'
			});
			setscreen('error');
			setmodal(null);
			setpopout(null);
			setconnection(0);
		});

		window.socket.connect();
	};



	const events = (socket) => {
		socket.on(`connect`, () => {
			setconnection(1)
		})
		socket.on(`disconnect`, (reason) => {
			setmodal(null)
			if (reason === "io client disconnect" || reason === "forced close") {
				window.socket.connect()
			} else {
				seterrorscreen({
					'num_error': 2,
					'error_msg': '–†–∞–∑–æ—Ä–≤–∞–Ω–æ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ —Å —Å–µ—Ä–≤–µ—Ä–æ–º'
				})
				setconnection(0)
				setscreen('error')
				setmodal(null)
			}
		})
		socket.on(`error`, () => {
			setmodal(null)
			seterrorscreen({
				'num_error': 2,
				'error_msg': '–†–∞–∑–æ—Ä–≤–∞–Ω–æ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ —Å —Å–µ—Ä–≤–µ—Ä–æ–º'
			})
			setscreen('error')
		})
		socket.on(`response`, (msg) => {

			if (msg.type == 'errorjoinroom') {
				seterrorscreen({
					'num_error': 2,
					'error_msg': '–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏–µ –∫ –∏–≥—Ä–µ'
				})
				setTimeout(() => {
					setwheeldata(msg)
					setpopout(null)
					setmodal(null)
					setscreen('error')
					clearTimeout()
				}, 500)
			}


			if (msg.type == 'online') {
				setonline(msg.count)
			}

			if (msg.type == 'datatop') {

				setmetop(msg.mydata)
				setpopout(null)
				settop(msg.array)
			}
			if (msg.type == 'updatebalance') {
				setuserdata({
					'balance': msg.balance,
					'bonus': msg.bonus
				})
				setloading(0)
			}

			if (msg.type == 'errorads') {
				setmodal('errstars_ads')
			}

			if (msg.type == 'successads') {
				setTimeout(() => {
					setsnackbar(<Snackbar
						style={{ marginBottom: 48 }}
						onClose={() => setsnackbar(null)}
						mode='dark'
						before={<Icon48WritebarDone fill="#8BC34A" />}
					>
						–í–∞–º –≤—ã–¥–∞–Ω –±–æ–Ω—É—Å –≤ —Ä–∞–∑–º–µ—Ä–µ {msg.sum} OC!
					</Snackbar>)
					clearTimeout()
				}, 250)
				setuserdata({
					'balance': msg.balance
				})
			}


			if (msg.type == 'userdata') {
				setuserdata(msg)
				setloading(false)
				if (msg.ban == 0) {
					setpopout(null)
				}
				if (msg.ban == 1) {
					seterrorscreen({
						'num_error': 3,
						'error_msg': '–°–µ—Ä–≤–µ—Ä –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω. –í —Ç–µ—á–µ–Ω–∏–µ –Ω–µ—Å–∫–æ–ª—å–∫–∏—Ö –º–∏–Ω—É—Ç –≤—Å—ë –¥–æ–ª–∂–Ω–æ –∑–∞—Ä–∞–±–æ—Ç–∞—Ç—å. –°–ª–µ–¥–∏—Ç–µ –∑–∞ –Ω–æ–≤–æ—Å—Ç—è–º–∏ –≤ –Ω–∞—à–µ–π –≥—Ä—É–ø–ø–µ.'
					})
					setpopout(null)
					setconnection(0)
					setscreen('error')
					setmodal(null)
				}
			}
			if (msg.type == 'updateuserdata') {
				setuserdata(msg)
			}
		})
	}

	const changeUnder = (e) => {
		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': '–°–µ—Ä–≤–µ—Ä –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω. –í —Ç–µ—á–µ–Ω–∏–µ –Ω–µ—Å–∫–æ–ª—å–∫–∏—Ö –º–∏–Ω—É—Ç –≤—Å—ë –¥–æ–ª–∂–Ω–æ –∑–∞—Ä–∞–±–æ—Ç–∞—Ç—å. –°–ª–µ–¥–∏—Ç–µ –∑–∞ –Ω–æ–≤–æ—Å—Ç—è–º–∏ –≤ –Ω–∞—à–µ–π –≥—Ä—É–ø–ø–µ.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		}
		setsnackbar(null)
		if (e.currentTarget.dataset.story == 'rating') {
			setpopout(<ScreenSpinner size='large' />)
			window.socket.emit(`request`, {
				'type': 'gettop'
			})
		}
		setactiveunder(e.currentTarget.dataset.story)
	}

	const updatehome = () => {
		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': '–°–µ—Ä–≤–µ—Ä –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω. –í —Ç–µ—á–µ–Ω–∏–µ –Ω–µ—Å–∫–æ–ª—å–∫–∏—Ö –º–∏–Ω—É—Ç –≤—Å—ë –¥–æ–ª–∂–Ω–æ –∑–∞—Ä–∞–±–æ—Ç–∞—Ç—å. –°–ª–µ–¥–∏—Ç–µ –∑–∞ –Ω–æ–≤–æ—Å—Ç—è–º–∏ –≤ –Ω–∞—à–µ–π –≥—Ä—É–ø–ø–µ.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		}
		setloading(true)
		window.socket.emit(`request`, {
			'type': 'getuserbalance'
		})
	}

	const go = (e) => {
		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': '–°–µ—Ä–≤–µ—Ä –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω. –í —Ç–µ—á–µ–Ω–∏–µ –Ω–µ—Å–∫–æ–ª—å–∫–∏—Ö –º–∏–Ω—É—Ç –≤—Å—ë –¥–æ–ª–∂–Ω–æ –∑–∞—Ä–∞–±–æ—Ç–∞—Ç—å. –°–ª–µ–¥–∏—Ç–µ –∑–∞ –Ω–æ–≤–æ—Å—Ç—è–º–∏ –≤ –Ω–∞—à–µ–π –≥—Ä—É–ø–ø–µ.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		}
		window.history.pushState({ panel: e.currentTarget.dataset.to }, `${e.currentTarget.dataset.to}`);
		setscreen(e.currentTarget.dataset.to)
		if (connection == 0) {
			seterrorscreen({
				'num_error': 2,
				'error_msg': '–†–∞–∑–æ—Ä–≤–∞–Ω–æ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ —Å —Å–µ—Ä–≤–µ—Ä–æ–º'
			})
			setTimeout(() => {
				setscreen('error')
				clearTimeout()
			}, 300)
		}
		setsnackbar(null)
	};

	const menu = () => {


		if (userdata.ban == 1) {
			seterrorscreen({
				'num_error': 3,
				'error_msg': '–°–µ—Ä–≤–µ—Ä –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–µ–Ω. –í —Ç–µ—á–µ–Ω–∏–µ –Ω–µ—Å–∫–æ–ª—å–∫–∏—Ö –º–∏–Ω—É—Ç –≤—Å—ë –¥–æ–ª–∂–Ω–æ –∑–∞—Ä–∞–±–æ—Ç–∞—Ç—å. –°–ª–µ–¥–∏—Ç–µ –∑–∞ –Ω–æ–≤–æ—Å—Ç—è–º–∏ –≤ –Ω–∞—à–µ–π –≥—Ä—É–ø–ø–µ.'
			})
			setconnection(0)
			setpopout(null)
			setscreen('error')
			setmodal(null)
			return
		} else {
			setsnackbar(null)
			window.socket.emit(`request`, {
				'type': 'leavegameroom',
				'room': 'all'
			})
			setmodal(null)
			setscreen('home')
			window.history.pushState({ screen: 'home' }, `home`);
		}
	}


	const videobonus = async () => {
		if (adsCooldown.current > 0) {
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FFA000" />}
				>
					–†–µ–∫–ª–∞–º–∞ –¥–æ—Å—Ç—É–ø–Ω–∞ —á–µ—Ä–µ–∑ {adsCooldown.current} —Å–µ–∫.
				</Snackbar>
			);
			return;
		}

		if (!window.show_9491456) {
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FF5C5C" />}
				>
					–†–µ–∫–ª–∞–º–∞ –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–Ω–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.
				</Snackbar>
			);
			return;
		}

		try {
			setpopout(<ScreenSpinner size="large" />);
			await window.show_9491456(); // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º —Ä–µ–∫–ª–∞–º—É

			window.socket.emit('request', {
				type: 'stars_ads',
				data: { result: true },
				prov: uniquecode
			});

			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon48WritebarDone fill="#4CAF50" />}
				>
					–í–∞–º –Ω–∞—á–∏—Å–ª–µ–Ω–æ 0.10 –∑–≤–µ–∑–¥—ã!
				</Snackbar>
			);

			// ‚è±Ô∏è –ö—É–ª–¥–∞—É–Ω
			adsCooldown.current = 5;
			adsCooldownTimer.current = setInterval(() => {
				adsCooldown.current--;
				if (adsCooldown.current <= 0) {
					clearInterval(adsCooldownTimer.current);
				}
			}, 1000);

		} catch (error) {
			// –ï—Å–ª–∏ —Ä–µ–∫–ª–∞–º–∞ –Ω–µ –±—ã–ª–∞ –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–∞ –∏–ª–∏ –≤–æ–∑–Ω–∏–∫–ª–∞ –æ—à–∏–±–∫–∞, –ø–æ–∫–∞–∂–µ–º –æ—à–∏–±–∫—É:
			setsnackbar(
				<Snackbar
					style={{ marginBottom: 48 }}
					onClose={() => setsnackbar(null)}
					mode='dark'
					before={<Icon56ErrorOutline fill="#FF5C5C" />}
				>
					–†–µ–∫–ª–∞–º–∞ –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ–¥–æ—Å—Ç—É–ø–Ω–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.
				</Snackbar>
			);

			window.socket.emit('request', {
				type: 'stars_ads',
				data: { result: false },
				prov: uniquecode
			});
		} finally {
			setpopout(null);
		}
	};


	const modal = (
		<ModalRoot activeModal={activemodal}>
			<ModalCard
				id='errads'
				onClose={() => setmodal(null)}
				icon={<Icon56ErrorOutline fill="#FF5C5C" />}
				header="–û—à–∏–±–∫–∞"
				subheader="–î–∞–Ω–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–æ—Å—Ç—É–ø–Ω–∞ —Ç–æ–ª—å–∫–æ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞—Ö."
				actions={
					<Button size="l" style={{ background: '#3498DB', borderRadius: 2 }} onClick={() => setmodal(null)}>
						–ó–∞–∫—Ä—ã—Ç—å
					</Button>
				}
			></ModalCard>


			<ModalCard
				id='referral'
				onClose={() => setmodal(null)}
				icon={<Icon56Users3Outline fill="#3498DB" />}
				header="–†–µ—Ñ–µ—Ä–∞–ª—å–Ω–∞—è —Å—Å—ã–ª–∫–∞"
				subheader={
					<div style={{ wordBreak: 'break-all', textAlign: 'center' }}>
						–î–µ–ª–∏—Å—å —Å—Å—ã–ª–∫–æ–π ‚Äî –∑–∞ –∫–∞–∂–¥–æ–≥–æ –ø—Ä–∏–≥–ª–∞—à—ë–Ω–Ω–æ–≥–æ —Ç—ã –ø–æ–ª—É—á–∏—à—å <b>2 –∑–≤–µ–∑–¥—ã</b> üåü
					</div>
				}
				actions={
					<div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
						<Button
							size="l"
							style={{ background: '#3b121fce', color: 'white', borderRadius: 15 }}
							onClick={(e) => {
								const textArea = document.createElement("textarea");
								textArea.value = 'https://t.me/wave_stars_bot?start=' + fetchedUser.id;
								document.body.appendChild(textArea);
								textArea.select();
								document.execCommand("copy");
								document.body.removeChild(textArea);

								const button = e.target;
								const originalText = button.innerText;
								button.innerText = '–°–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–æ!';
								setTimeout(() => {
									button.innerText = originalText;
								}, 2000);
							}}
						>
							–°–∫–æ–ø–∏—Ä–æ–≤–∞—Ç—å —Å—Å—ã–ª–∫—É
						</Button>
					</div>
				}
			/>


		</ModalRoot>
	);

	return (
		<ConfigProvider platform="ios">
			<AdaptivityProvider>
				<AppRoot>
					<View activePanel={screen} modal={modal} popout={popout}>
						<Home id="home" metop={metop} typemodal={setmodal} videobonus={videobonus} datatop={datatop} online={online} connection={connection} userdata={userdata} go={go} activeunder={activeunder} loading={loading} fetchedUser={fetchedUser} changeUnder={changeUnder} updatehome={updatehome} snackbar={snackbar} />
						<Error id="error" go={go} data={errorscreen} />
					</View>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>
	);
}


export default App;
